@font-face {
  font-family: "Montserrat";
  src: local("Montserrat-Medium"), url("../fonts/Montserrat-Medium.ttf") format("truetype");
  font-weight: 500;
  font-display: swap;
}
body {
  margin: 0 auto;
  font-size: 15px;
  line-height: 20px;
  color: #000000;
  background-color: #f5f5f5;
  font-family: "Montserrat", "Arial", sans-serif;
}
.container {
  width: 302px;
  margin: 0 auto;
}
.delivery {
  padding-top: 20px;
  padding-bottom: 140px;
}
.delivery__title {
  margin-bottom: 45px;
  font-size: 24px;
  line-height: 30px;
}
.delivery__list {
  list-style: none;
  margin: 0;
  padding: 0;
}
.delivery__wrapper {
  padding: 0 15px;
  background-color: #ffffff;
  border-top: 1px solid #dbdeef;
}
.title-wrapper {
  position: relative;
  display: flex;
  align-items: center;
  height: 54px;
  cursor: pointer;
}
.title-wrapper--open {
  color: #292ffa;
}
.title-wrapper--open:after {
  position: absolute;
  content: "";
  background-repeat: no-repeat;
  top: 27px;
  right: 15px;
  width: 9px;
  height: 10px;
  transform: rotate(45deg);
  border-top: 2px solid #292ffa;
  border-left: 2px solid #292ffa;
}
.title-wrapper--close {
  color: #82879f;
}
.title-wrapper--close:after {
  position: absolute;
  content: "";
  background-repeat: no-repeat;
  top: 20px;
  right: 15px;
  width: 9px;
  height: 10px;
  transform: rotate(45deg);
  border-bottom: 2px solid #8387a0;
  border-right: 2px solid #8387a0;
}
.title-wrapper:hover h3 {
  color: #292ffa;
}
.title-wrapper--close:hover:after {
  border-color: #292ffa;
}
.delivery__wrapper h3 {
  font-size: 14px;
  line-height: 20px;
}
.delivery__form {
  padding-bottom: 35px;
}
.delivery__form label {
  font-size: 14px;
  line-height: 26px;
}
.delivery__button {
  display: block;
  margin: 0 auto;
  width: 210px;
  height: 56px;
  border-radius: 28px;
  background-color: #292ffa;
  border: none;
  font-size: 16px;
  line-height: 24px;
  color: #ffffff;
}
.delivery__button:hover,
.delivery__button:focus {
  background-color: #282de5;
}
.delivery__button:active {
  color: #252ad3;
}
.delivery__button:disabled {
  color: #ededee;
}
.user-info {
  padding-top: 20px;
}
.user-info__fieldset {
  margin: 0;
  padding: 0;
  margin-bottom: 35px;
  display: flex;
  flex-direction: column;
  border: none;
}
.user-info__fieldset label {
  margin-bottom: 10px;
}
.user-info__fieldset input,
.user-info__fieldset textarea {
  padding-left: 2.8%;
  width: 97.2%;
  border: 1px solid #daddef;
  border-radius: 3px;
}
.user-info__fieldset input:hover,
.user-info__fieldset textarea:hover {
  cursor: pointer;
}
.user-info__fieldset input:focus,
.user-info__fieldset textarea:focus {
  border-color: #8387a0;
  cursor: text;
}
.user-info__fieldset input:disabled,
.user-info__fieldset textarea:disabled {
  background-color: #ededee;
  border-color: #ffffff;
}
.user-info__fieldset input::-webkit-input-placeholder {
  font-size: 16px;
  line-height: 26px;
  color: #daddef;
}
.user-info__fieldset input::-moz-placeholder {
  font-size: 16px;
  line-height: 26px;
  color: #daddef;
}
/* Firefox 19+ */
.user-info__fieldset input:-moz-placeholder {
  font-size: 16px;
  line-height: 26px;
  color: #daddef;
}
/* Firefox 18- */
.user-info__fieldset input:-ms-input-placeholder {
  font-size: 16px;
  line-height: 26px;
  color: #daddef;
}
.user-info__fieldset input {
  height: 55px;
}
.user-info__fieldset textarea {
  margin-bottom: 40px;
  padding-top: 15px;
  height: 110px;
  resize: none;
}
.delivery-address {
  padding-top: 30px;
}
.delivery-address__list {
  list-style: none;
  margin: 0;
  padding: 0;
}
.delivery-address__item {
  margin-bottom: 30px;
}
.delivery-address input {
  display: none;
}
.delivery-address label {
  position: relative;
  margin-left: 50px;
  cursor: pointer;
  outline: none;
}
.delivery-address label:before {
  position: absolute;
  content: "";
  background-repeat: no-repeat;
  top: -2px;
  left: -40px;
  width: 6px;
  height: 6px;
  border-radius: 50%;
  border: 6px solid #ffffff;
  box-shadow: 0 0 0 2px #dee1f1;
  background-color: #ffffff;
}
.delivery-address label:hover,
.delivery-address label:focus {
  color: #2e34fa;
}
.delivery-address label:hover:before,
.delivery-address label:focus:before {
  box-shadow: 0 0 0 2px #2e34fa;
}
.delivery-address label:active:before {
  background-color: #2e34fa;
}
.delivery-address input:checked ~ label:before {
  box-shadow: 0 0 0 2px #2e34fa;
  background-color: #2e34fa;
}
.delivery-address input:disabled ~ label:before {
  border: 6px solid #dee1f1;
  box-shadow: 0 0 0 2px #dee1f1;
  background-color: #dee1f1;
}
.map {
  margin-bottom: 40px;
  width: 100%;
  height: 305px;
}
.wrapper-open {
  display: block;
}
.wrapper-close {
  display: none;
}
@media (min-width: 768px) {
  .container {
    width: 702px;
  }
  .delivery__list {
    position: relative;
    display: flex;
  }
  .delivery__wrapper {
    padding: 0 33px;
    border-top: none;
  }
  .delivery__title {
    font-size: 32px;
  }
  .title-wrapper {
    width: 70px;
    height: 65px;
    border-radius: 5px 5px 0 0;
  }
  .title-wrapper:before {
    position: absolute;
    content: "";
    background-repeat: no-repeat;
    top: -2px;
    right: -35px;
    width: 0;
    height: 0;
    border-bottom: 67px solid white;
    border-right: 35px solid transparent;
  }
  .title-wrapper:after {
    display: none;
  }
  .title-wrapper--open {
    z-index: 2;
  }
  .title-wrapper--close {
    background-color: #ededee;
  }
  .title-wrapper--close:before {
    border-bottom: 67px solid #ededee;
  }
  .user-info__wrapper,
  .delivery-address__wrapper {
    position: absolute;
    width: 90%;
    left: 0;
  }
  .title-wrapper h3:after {
    position: absolute;
    content: "";
    background-repeat: no-repeat;
    top: -1px;
    right: -19px;
    width: 2px;
    height: 70px;
    transform: rotate(-26deg);
  }
  .title-wrapper--close h3 {
    color: #8387a0;
  }
  .title-wrapper--close h3:after {
    background-color: #e3e3e3;
  }
  .title-wrapper--open h3:after {
    background-color: #dadadb;
  }
  .delivery-address__list {
    display: flex;
    flex-wrap: wrap;
  }
  .delivery-address__item {
    width: 50%;
  }
  .map {
    height: 550px;
  }
  .delivery__button {
    margin-right: 0;
  }
}
@media (min-width: 1200px) {
  .container {
    width: 1000px;
  }
}
